import { supabase } from "@/lib/supabase";
import { notFound } from "next/navigation";
import { Metadata, ResolvingMetadata } from "next"; 
import StorePageWrapper from "@/components/store/StorePageWrapper"; // The client-side wrapper
import ViewTracker from "@/components/ViewTracker"; // The View Counter component

interface PageProps {
  params: Promise<{ slug: string }>;
}

// 1. DYNAMIC METADATA GENERATOR (RESTORED)
export async function generateMetadata(
  { params }: PageProps,
  parent: ResolvingMetadata
): Promise<Metadata> {
  const resolvedParams = await params;
  const slug = resolvedParams.slug;

  // Fetch store details for the preview
  const { data: store } = await supabase.from("stores").select("name, description, logo_url, cover_image_url").eq("slug", slug).single();

  if (!store) {
    return {
      title: "Store Not Found",
    };
  }

  return {
    title: `${store.name} | StoreLink`,
    description: store.description || `Shop at ${store.name}`,
    openGraph: {
      title: store.name,
      description: store.description || "Check out my store on StoreLink!",
      images: [store.cover_image_url || store.logo_url || "/default-cover.jpg"], // Fallback image
    },
  };
}

// 2. MAIN PAGE COMPONENT (RESTORED VIEW TRACKER AND CATEGORY FETCHING)
export default async function StorePage({ params }: PageProps) {
  const resolvedParams = await params;
  const slug = resolvedParams.slug;

  // 1. Get Store
  const { data: store } = await supabase
    .from("stores")
    .select("*")
    .eq("slug", slug)
    .single();

  if (!store) return notFound();

  // 2. Get Products (Using the View to respect subscription limits/expiry)
  const { data: products } = await supabase
    .from("storefront_products") 
    .select("*, categories(name)") // <--- Also fetching category names now
    .eq("store_id", store.id)
    .order("created_at", { ascending: false });

  // 3. Render Wrapper with ViewTracker
  return (
    <>
      {/* RESTORED: View Tracker runs on the server, but updates the database */}
      <ViewTracker storeId={store.id} /> 
      
      <StorePageWrapper 
        store={store} 
        products={products || []} 
        // Categories are now implicitly included in the products object via the join above, 
        // making the wrapper simpler, but we can pass all product data.
      />
    </>
  );
}